@import './variables.scss';

.request__form {
  width: 700px;
  height: 389px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.5);
  background-color: #ffffff;
  margin: 22px 250px 0 490px;

  &--title {
    margin: 31px auto 17px;
    width: 283px;
    // height: 29px;
    font-family: 'Graphik-Bold-Web';
    font-size: 24px;
    margin: 31px 387px 17px 30px;
    color: $colorSecundary;
    padding-top: 31px;
    font-weight: bold;
  }

  &--inputs {
    display: flex;
  }

  &--label {
    font-family: 'Graphik-Medium-Web';
    font-size: 12px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.1px;
    color: #3d4750;
  }

  &--label.date {
    height: 14px;
    margin-bottom: 7px;
  }

  &--input {
    font-size: 14px;
    height: 45px;
    width: 310px;
    background-color: #eeeeee;
    border: solid 1px #ebeced;
    color: #b1b1b1;

    &::placeholder {
      padding-left: 19px;
      //font-family: Graphik;
      font-size: 14px;
      font-weight: 500;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 0.1px;
    }
  }

  &--submitButton {
    width: 150px;
    height: 36px;
    border-radius: 5px;
    background-color: #eeeeee;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0px 280px 29px 270px;
    color: #b1b1b1;
    border-style: none;
  }
}

.left-inputs {
  margin-left: 30px;
}

.request__job {
  margin: 22px 16px 20px auto;
  border-radius: 5px;
  border: solid 1px #ebeced;
  background-color: #ffffff;

  &::placeholder {
    font-family: Graphik;
    font-size: 14px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.29;
    letter-spacing: normal;
  }
}

.request__chapter {
  border-radius: 5px;
  border: solid 1px #ebeced;
  background-color: #ffffff;

  &::placeholder {
    font-family: Graphik;
    font-size: 14px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.29;
    letter-spacing: normal;
  }
}

.request__text {
  display: flex;
  margin: 0 0 20px;
  width: 310px;
  height: 112px;
  border: solid 1px #ebeced;
  background-color: #eeeeee;

  &::placeholder {
    display: block;
    top: 0;
    padding: 6px 6px 0 20px;
  }
}

.wrap__aside {
  display: grid;
  grid-column: 1 / 4;
}

.wrap__form {
  display: grid;
  //padding: 0 75px 0 30px;
  //height: 30px;
  grid-column: 4 / 12;
}

.request__wrap {
  display: grid;
  height: 900px;
}
